import { useState } from "react";
import { Data } from "./Data";
import "./App.css";

function App() {
  let [count, setCount] = useState(0);
  const [text, setText] = useState([]);

  const generateText = (e) => {
    e.preventDefault();
    if (count <= 0) {
      count = 1;
    }
    if (count > 8) {
      count = 8;
    }
    const amt = parseInt(count);
    setText(Data.slice(0, amt));
  };

  return (
    <div className="App">
      <div className="container">
        <div className="all-section">
          <h1 className="heading">Tired of Boring Lorem Ipsum?</h1>
          <p className="para">Generate dummy text for your projects.</p>
          <form onSubmit={generateText}>
            <div className="wrapper">
              <input
                type="number"
                value={count}
                onChange={(e) => setCount(e.target.value)}
              />
              <button type="submit">Generate</button>
            </div>
          </form>
          <div className="text-container">
            {text.map((item, index) => {
              return <p key={index}>{item}</p>;
            })}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
